___CSS_LOADER_EXPORT___.push([module.id, ".item-container[data-v-51998932]{max-width:30rem;margin:0}.item[data-v-51998932]{padding:1rem;border:1px solid #000;background-color:#fefefe}.item-sub[data-v-51998932]{margin:0 0 0 1rem}.btn .handle[data-v-51998932]{cursor:move}", "",{"version":3,"sources":["webpack://./resources/assets/js/components/menus/menu-items.vue"],"names":[],"mappings":"AAkJA,iCACA,eAAA,CACA,QACA,CACA,uBACA,YAAA,CACA,qBAAA,CACA,wBACA,CACA,2BACA,iBACA,CACA,8BACA,WACA","sourcesContent":["<template>\n    <draggable\n        v-bind=\"dragOptions\"\n        tag=\"div\"\n        class=\"item-container\"\n        :list=\"list\"\n        :value=\"value\"\n        @input=\"emitter\"\n        @change=\"log\"\n    >\n        <div class=\"item-group\" :key=\"menuItem.id\" v-for=\"menuItem in realValue\">\n            <div class=\"item\" :class=\"[isEditing(menuItem.id) ? 'd-none' : 'd-flex']\">\n                <span class=\"btn btn-link handle\" title=\"Move\"><i class=\"fas fa-arrows-alt\"></i></span>\n                <input type=\"text\" readonly class=\"form-control-plaintext\" :value=\"menuItem.name\">\n                <input type=\"hidden\" :name=\"parentName + '[' + menuItem.id + '][id]'\" :value=\"menuItem.id\">\n                <div class=\"d-inline-block ml-auto\">\n                    <div class=\"btn-group\" role=\"group\">\n                        <span v-if=\"menuItem.actions.update\" class=\"btn btn-outline-success\" @click=\"startEdit(menuItem.id)\" title=\"Edit\"><i class=\"fas fa-pencil-alt\"></i></span>\n                        <span v-if=\"menuItem.actions.delete\" class=\"btn btn-outline-danger\" @click=\"deleteMenuItem(menuItem)\" title=\"Delete\"><i class=\"fas fa-trash-alt\"></i></span>\n                    </div>\n                </div>\n            </div>\n            <div class=\"item\" :class=\"[isEditing(menuItem.id) ? 'd-flex' : 'd-none']\">\n                <div class=\"input-group\">\n                    <input type=\"text\" class=\"form-control\" :value=\"menuItem.name\" @input=\"change($event)\">\n                    <div class=\"input-group-append\">\n                        <span class=\"btn btn-outline-success\" @click=\"finishEdit(menuItem)\" title=\"Save\"><i class=\"fas fa-check\"></i></span>\n                        <span class=\"btn btn-outline-danger\" @click=\"cancelEdit\" title=\"Cancel\"><i class=\"fas fa-times\"></i></span>\n                    </div>\n                </div>\n            </div>\n            <menu-items class=\"item-sub\" :parentMenuItemId=\"menuItem.id\" :list=\"menuItem.menu_items\" :parentName=\"parentName + '[' + menuItem.id + '][menu_items]'\" />\n        </div>\n    </draggable>\n</template>\n\n<script>\nimport draggable from 'vuedraggable';\nimport _ from \"lodash\";\nimport {mapGetters} from \"vuex\";\n\nexport default {\n    name: \"menuItems\",\n    components: {\n        draggable\n    },\n    props: {\n        value: {\n            required: false,\n            type: Array,\n            default: null\n        },\n        list: {\n            required: false,\n            type: Array,\n            default: null\n        },\n        parentName: {\n            required: true,\n            type: String,\n        },\n        parentMenuItemId: {\n            required: false,\n            type: Number,\n            default: null,\n        }\n    },\n\n    data() {\n        return {\n            editing: {\n                id: null,\n                value: null,\n            },\n        };\n\n    },\n\n    methods: {\n        startEdit(menu_item_id) {\n            this.editing.id = menu_item_id;\n        },\n        isEditing(menu_item_id) {\n            return this.editing.id === menu_item_id;\n        },\n        change(event) {\n            this.editing.value = event.target.value;\n        },\n        emitter(value) {\n            this.$emit(\"input\", value);\n        },\n        finishEdit(menuItem) {\n            menuItem.name = this.editing.value;\n            this.$store.dispatch('menus/updateMenuItem', {\n                menuItem: menuItem,\n                order: menuItem.order,\n                to_menu_item_id: this.parentMenuItemId,\n            }).then(() => {\n                this.editing.id = null;\n                this.editing.value = null;\n            });\n        },\n        cancelEdit()\n        {\n            this.editing.id = null;\n            this.editing.value = null;\n        },\n        log(value) {\n            if (value.hasOwnProperty('added')) {\n                this.$store.dispatch('menus/updateMenuItem', {\n                    menuItem: value.added.element,\n                    order: value.added.newIndex,\n                    to_menu_item_id: this.parentMenuItemId,\n                });\n            } else if (value.hasOwnProperty('moved')) {\n                this.$store.dispatch('menus/updateMenuItem', {\n                    menuItem: value.moved.element,\n                    order: value.moved.newIndex,\n                    to_menu_item_id: this.parentMenuItemId,\n                });\n            }\n        },\n        deleteMenuItem(menuItem) {\n            this.$store.dispatch('menus/deleteMenuItem', menuItem);\n        },\n    },\n    computed: {\n        ...mapGetters('menus', [\n            'menu',\n        ]),\n        dragOptions() {\n            return {\n                animation: 0,\n                group: \"description\",\n                disabled: false,\n                ghostClass: \"ghost\"\n            };\n        },\n        realValue() {\n            return this.value ? this.value : this.list;\n        },\n    },\n}\n</script>\n\n<style scoped>\n    .item-container {\n        max-width: 30rem;\n        margin: 0;\n    }\n    .item {\n        padding: 1rem;\n        border: solid black 1px;\n        background-color: #fefefe;\n    }\n    .item-sub {\n        margin: 0 0 0 1rem;\n    }\n    .btn .handle {\n        cursor: move;\n    }\n</style>\n"],"sourceRoot":""}]);
